.businessMoving{padding-top:80px}.hero{background:linear-gradient(135deg, var(--primary), var(--dark));color:#fff;padding:4rem 2rem;text-align:center;position:relative;overflow:hidden}.hero::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:radial-gradient(circle at center, transparent 0%, rgba(0, 0, 0, 0.3) 100%);animation:pulse 4s ease-in-out infinite}.hero h1{font-size:clamp(2rem,5vw,3.5rem);margin-bottom:1rem;position:relative;display:inline-block;animation:glitter 8s linear infinite}.hero h1::before{content:"Betriebsumzüge";position:absolute;left:0;width:0;height:100%;color:var(--accent);overflow:hidden;animation:revealText 4s linear infinite}.hero h1::after{content:"";position:absolute;left:0;top:0;width:100%;height:100%;background:linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);animation:shine 3s linear infinite}.hero p{font-size:clamp(1.1rem,2vw,1.25rem);max-width:800px;margin:0 auto;opacity:0;animation:fadeIn 1s ease forwards .5s}.container{max-width:1200px;margin:0 auto;padding:4rem 2rem;position:relative}.container h2{position:relative;display:inline-block;margin-bottom:2rem}.container h2::after{content:"";position:absolute;bottom:-10px;left:0;width:50px;height:3px;background:var(--accent);animation:expandWidth 2s ease-in-out infinite}.container::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:radial-gradient(circle at 20% 20%, rgba(218, 165, 32, 0.05) 0%, transparent 30%),radial-gradient(circle at 80% 80%, rgba(218, 165, 32, 0.05) 0%, transparent 30%);animation:backgroundShimmer 10s ease-in-out infinite alternate}.featureGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:2rem;margin-top:3rem}.featureCard{background:var(--primary-dark);padding:2rem;border-radius:8px;box-shadow:0 4px 15px rgba(0,0,0,.2);text-align:center;transition:all .5s cubic-bezier(0.4, 0, 0.2, 1);border:1px solid hsla(0,0%,100%,.1);position:relative;overflow:hidden}.featureCard::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.05), transparent);transform:translateX(-100%);transition:transform .5s}.featureCard:hover{transform:translateY(-10px) scale(1.02);box-shadow:0 8px 25px rgba(0,0,0,.3);border-color:var(--accent)}.featureCard:hover::before{transform:translateX(100%)}.featureCard:hover i{transform:rotateY(360deg)}.featureCard i{font-size:2rem;color:var(--accent);margin-bottom:1rem;transition:transform 1s ease}.featureCard h3{margin-bottom:1rem;color:#fff}.featureCard p{color:hsla(0,0%,100%,.8);line-height:1.6}.steps{margin-top:3rem}.step{display:flex;align-items:flex-start;gap:2rem;margin-bottom:2rem;padding:2rem;background:var(--primary-dark);border-radius:8px;box-shadow:0 4px 15px rgba(0,0,0,.2);transition:all .5s cubic-bezier(0.4, 0, 0.2, 1);border:1px solid hsla(0,0%,100%,.1);position:relative;overflow:hidden}.step::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.05), transparent);transform:translateX(-100%);transition:transform .5s}.step:hover{transform:translateX(10px) scale(1.02);border-color:var(--accent)}.step:hover::before{transform:translateX(100%)}.step:hover .stepNumber{animation:pulse 2s infinite}.step .stepContent h3{color:#fff;margin-bottom:.5rem;transform:translateY(0);transition:transform .3s ease}.step .stepContent p{color:hsla(0,0%,100%,.8);line-height:1.6;transform:translateY(0);transition:transform .3s ease}.step:hover .stepContent h3{transform:translateY(-2px)}.step:hover .stepContent p{transform:translateY(-2px)}.stepNumber{width:40px;height:40px;background:var(--accent);color:#fff;border-radius:50%;display:flex;align-items:center;justify-content:center;font-weight:bold;flex-shrink:0;transition:all .3s ease}.specialServicesGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:2rem;margin-top:3rem}.specialServiceCard{background:var(--primary-dark);color:#fff;padding:2rem;border-radius:8px;text-align:center;transition:all .5s cubic-bezier(0.4, 0, 0.2, 1);border:1px solid hsla(0,0%,100%,.1);position:relative;overflow:hidden;transform-style:preserve-3d;perspective:1000px}.specialServiceCard::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.05), transparent);transform:translateX(-100%);transition:transform .5s}.specialServiceCard::after{content:"";position:absolute;top:-50%;left:-50%;width:200%;height:200%;background:radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);opacity:0;transition:opacity .5s}.specialServiceCard:hover{transform:translateY(-5px) rotateX(10deg);border-color:var(--accent);box-shadow:0 8px 25px rgba(0,0,0,.2),0 0 0 1px var(--accent),0 0 20px rgba(218,165,32,.2)}.specialServiceCard:hover::before{transform:translateX(100%)}.specialServiceCard:hover::after{opacity:1;animation:pulseGlow 2s ease-in-out infinite}.specialServiceCard:hover i{animation:iconFloat 2s ease infinite;color:var(--accent);text-shadow:0 0 20px var(--accent)}.specialServiceCard:hover h3{color:var(--accent);transform:translateZ(30px)}.specialServiceCard:hover p{transform:translateZ(20px)}.specialServiceCard i{font-size:2.5rem;color:var(--accent);margin-bottom:1.5rem;transition:all .3s ease;display:inline-block;transform:translateZ(20px)}.specialServiceCard h3{margin-bottom:1rem;color:#fff;transition:all .3s ease;transform:translateZ(10px);position:relative}.specialServiceCard h3::after{content:"";position:absolute;bottom:-5px;left:50%;transform:translateX(-50%);width:0;height:2px;background:var(--accent);transition:width .3s ease}.specialServiceCard:hover h3::after{width:50%}.specialServiceCard p{opacity:.9;line-height:1.6;color:hsla(0,0%,100%,.8);transition:all .3s ease;transform:translateZ(0)}.cta{background:var(--primary);color:#fff;text-align:center;padding:4rem 