}@keyframes iconFloat{0%,100%{transform:translateY(0) translateZ(20px)}50%{transform:translateY(-10px) translateZ(30px)}}@keyframes pulseGlow{0%,100%{opacity:.5}50%{opacity:1}}@keyframes backgroundShimmer{0%{opacity:.3;transform:scale(1)}100%{opacity:.6;transform:scale(1.1)}}