.termsContainer{min-height:100vh;background:var(--background-dark);color:var(--text-light);position:relative;overflow:hidden;padding:6rem 2rem 4rem}.termsContainer::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:radial-gradient(circle at 20% 20%, rgba(76, 175, 80, 0.03) 0%, transparent 50%),radial-gradient(circle at 80% 80%, rgba(76, 175, 80, 0.03) 0%, transparent 50%);pointer-events:none}.termsContent{max-width:900px;margin:0 auto;position:relative;z-index:1;background:hsla(0,0%,100%,.02);backdrop-filter:blur(10px);border-radius:20px;padding:3rem;border:1px solid hsla(0,0%,100%,.05);box-shadow:0 8px 32px rgba(0,0,0,.1)}.termsContent h1{font-size:2.5rem;margin-bottom:2rem;text-align:center;background:linear-gradient(135deg, #fff 0%, #4CAF50 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);filter:drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3))}.section{margin-bottom:3rem;padding-bottom:2rem;border-bottom:1px solid hsla(0,0%,100%,.1)}.section:last-child{border-bottom:none;margin-bottom:0;padding-bottom:0}.section h2{color:var(--primary-color);font-size:1.8rem;margin-bottom:1.5rem}.infoCard{background:hsla(0,0%,100%,.03);border-radius:12px;padding:1.5rem;margin-bottom:1.5rem;border:1px solid hsla(0,0%,100%,.05);transition:transform .3s ease}.infoCard:hover{transform:translateY(-2px);background:hsla(0,0%,100%,.04)}.infoCard h3{color:var(--text-light);font-size:1.2rem;margin-bottom:1rem}.infoCard p{color:hsla(0,0%,100%,.8);line-height:1.6;margin-bottom:1rem}.infoCard p:last-child{margin-bottom:0}.infoCard ul{list-style:none;padding-left:1rem}.infoCard ul li{color:hsla(0,0%,100%,.8);margin-bottom:.5rem;position:relative;padding-left:1.5rem}.infoCard ul li::before{content:"→";position:absolute;left:0;color:var(--primary-color)}.lastUpdate{margin-top:3rem;text-align:center;font-size:.9rem;color:hsla(0,0%,100%,.6)}.backgroundElements{position:fixed;inset:0;pointer-events:none;z-index:0}.floatingElement{position:absolute;width:400px;height:400px;background:radial-gradient(circle, rgba(76, 175, 80, 0.05) 0%, transparent 70%);border-radius:50%;filter:blur(50px);opacity:.5}@media(max-width: 768px){.termsContainer{padding:5rem 1rem 2rem}.termsContent{padding:2rem}h1{font-size:2rem}.section h2{font-size:1.5rem}}