.movingTipsContainer{min-height:100vh;background:var(--background-dark);color:var(--text-light);position:relative;overflow:hidden;padding-top:6rem}.movingTipsContainer::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:radial-gradient(circle at 20% 20%, rgba(76, 175, 80, 0.05) 0%, transparent 50%),radial-gradient(circle at 80% 80%, rgba(76, 175, 80, 0.05) 0%, transparent 50%);pointer-events:none}.hero{text-align:center;padding:4rem 2rem;position:relative}.hero h1{font-size:clamp(2rem,5vw,3.5rem);margin-bottom:1rem;background:linear-gradient(135deg, #fff 0%, #4CAF50 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);filter:drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3))}.hero p{font-size:1.2rem;color:hsla(0,0%,100%,.8);max-width:600px;margin:0 auto}.categoriesSection{max-width:1200px;margin:0 auto;padding:2rem}.category{margin-bottom:4rem}.category .categoryHeader{display:flex;align-items:center;gap:1rem;margin-bottom:2rem}.category .categoryHeader h2{font-size:1.8rem;color:var(--primary-color)}.categoryIcon{font-size:2rem;display:inline-block;padding:1rem;background:rgba(76,175,80,.1);border-radius:50%;border:2px solid rgba(76,175,80,.2);box-shadow:0 0 20px rgba(76,175,80,.1)}.tipsGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:2rem}.tipCard{background:hsla(0,0%,100%,.05);backdrop-filter:blur(10px);border-radius:15px;padding:2rem;border:1px solid hsla(0,0%,100%,.1);position:relative;overflow:hidden}.tipCard::before{content:"";position:absolute;top:0;left:0;right:0;height:2px;background:linear-gradient(90deg, transparent, rgba(76, 175, 80, 0.5), transparent);transform:translateX(-100%);animation:shimmer 3s infinite}.tipCard h3{color:var(--primary-color);font-size:1.3rem;margin-bottom:1rem}.tipCard p{color:hsla(0,0%,100%,.8);margin-bottom:1.5rem;line-height:1.6}.tipCard ul{list-style:none;padding:0}.tipCard ul li{color:hsla(0,0%,100%,.7);margin-bottom:.8rem;padding-left:1.5rem;position:relative}.tipCard ul li::before{content:"→";position:absolute;left:0;color:var(--primary-color)}.expertSection{background:rgba(0,0,0,.2);padding:4rem 2rem;margin-top:4rem;position:relative;overflow:hidden}.expertSection::before{content:"";position:absolute;inset:0;background:linear-gradient(45deg, rgba(76, 175, 80, 0.05) 0%, transparent 50%, rgba(76, 175, 80, 0.05) 100%);pointer-events:none}.expertSection h2{text-align:center;font-size:2rem;color:var(--primary-color);margin-bottom:3rem}.expertGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:2rem;max-width:1200px;margin:0 auto}.expertCard{background:hsla(0,0%,100%,.05);border-radius:15px;padding:2rem;text-align:center;border:1px solid hsla(0,0%,100%,.1);transition:all .3s ease}.expertCard .expertIcon{font-size:2.5rem;display:inline-block;margin-bottom:1rem;position:relative}.expertCard .expertIcon::after{content:"";position:absolute;inset:-10px;border-radius:50%;border:2px solid rgba(76,175,80,.3);animation:pulse 2s infinite}.expertCard h3{color:var(--text-light);font-size:1.2rem;margin-bottom:1rem}.expertCard p{color:hsla(0,0%,100%,.7);line-height:1.6}.ctaSection{text-align:center;padding:6rem 2rem;background:linear-gradient(to bottom, transparent, rgba(76, 175, 80, 0.1))}.ctaSection h2{font-size:2rem;color:var(--text-light);margin-bottom:1rem}.ctaSection p{color:hsla(0,0%,100%,.8);margin-bottom:2rem}.ctaButton{background:var(--primary-color);color:#fff;border:none;padding:1rem 2rem;font-size:1.1rem;border-radius:30px;cursor:pointer;position:relative;overflow:hidden}.ctaButton::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.2), transparent);transform:translateX(-100%);transition:transform .6s}.ctaButton:hover::before{transform:translateX(100%)}.backgroundElements{position:fixed;inset:0;pointer-events:none;z-index:0}.floatingElement{position:absolute;width:300px;height:300px;background:radial-gradient(circle, rgba(76, 175, 80, 0.1) 0%, transparent 70%);border-radius:50%;filter:blur(40px)}.glowingOrb{position:absolute;right:10%;top:20%;width:200px;height:200px;background:radial-gradient(circle, rgba(76, 175, 80, 0.2) 0%, transparent 70%);border-radius:50%;filter:blur(30px)}@keyframes shimmer{0%{transform:translateX(-100%)}100%{transform:translateX(100%)}}@keyframes pulse{0%{transform:scale(1);opacity:1}50%{transform:scale(1.2);opacity:.5}100%{transform:scale(1);opacity:1}}@media(max-width: 768px){.movingTipsContainer{padding-top:5rem}.hero{padding:3rem 1rem}.categoriesSection,.expertSection{padding:2rem 1rem}.tipsGrid,.expertGrid{grid-template-columns:1fr}.ctaSection{padding:4rem 1rem}}