.prices{padding-top:80px;background:var(--bg-dark);color:var(--text-primary)}.hero{background:linear-gradient(135deg, var(--primary-dark), var(--primary));padding:6rem 2rem;text-align:center;position:relative}.hero::after{content:"";position:absolute;bottom:0;left:0;right:0;height:4px;background:linear-gradient(to right, transparent, var(--accent), transparent)}.hero h1{font-size:clamp(2.5rem,5vw,4rem);margin-bottom:1.5rem;color:var(--text-primary)}.hero p{font-size:clamp(1.1rem,2vw,1.3rem);color:var(--text-secondary);max-width:600px;margin:0 auto}.packages{padding:6rem 2rem;max-width:1200px;margin:0 auto}.packageGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));gap:2rem;margin-bottom:4rem}.package{background:var(--bg-medium);border-radius:var(--border-radius);padding:3rem 2rem;position:relative;transition:transform .3s ease;border:1px solid hsla(0,0%,100%,.1)}.package:hover{transform:translateY(-5px)}.package.recommended{background:var(--bg-light);border:2px solid var(--accent)}.package h2{font-size:2rem;margin-bottom:1rem;color:var(--text-primary);text-align:center}.package .price{font-size:2.5rem;font-weight:700;color:var(--accent);text-align:center;margin-bottom:2rem;padding-bottom:2rem;border-bottom:1px solid hsla(0,0%,100%,.1)}.package ul{list-style:none;padding:0;margin:0 0 2rem}.package ul li{display:flex;align-items:center;gap:1rem;padding:.75rem 0;color:var(--text-secondary);font-size:1.1rem}.package ul li::before{content:"✓";color:var(--accent);font-weight:bold}.package .note{text-align:center;color:var(--text-muted);font-style:italic;margin-bottom:2rem}.recommendedBadge{position:absolute;top:1rem;right:1rem;background:var(--accent);color:var(--bg-dark);padding:.5rem 1rem;border-radius:20px;font-weight:600;font-size:.9rem}.additionalServices{background:var(--bg-medium);padding:4rem 0;margin:4rem 0}.additionalServices h2{text-align:center;color:var(--text-primary);margin-bottom:3rem;font-size:clamp(1.8rem,3vw,2.5rem)}.servicesGrid{max-width:800px;margin:0 auto;padding:0 2rem}.serviceItem{display:flex;justify-content:space-between;align-items:center;padding:1.5rem;border-bottom:1px solid hsla(0,0%,100%,.1);color:var(--text-secondary)}.serviceItem:last-child{border-bottom:none}.serviceItem .servicePrice{color:var(--accent);font-weight:600}.info{max-width:1200px;margin:0 auto;padding:4rem 2rem}.info h2{text-align:center;color:var(--text-primary);margin-bottom:3rem;font-size:clamp(1.8rem,3vw,2.5rem)}.infoGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:2rem}.infoItem{background:var(--bg-medium);padding:2rem;border-radius:var(--border-radius);text-align:center;border:1px solid hsla(0,0%,100%,.1)}.infoItem h3{color:var(--accent);margin-bottom:1rem;font-size:1.3rem}.infoItem p{color:var(--text-secondary);line-height:1.6}.button{display:inline-block;width:100%;padding:1rem;background:var(--accent);color:var(--bg-dark);text-align:center;text-decoration:none;border-radius:var(--border-radius);font-weight:600;transition:all .3s ease;margin-top:1rem}.button:hover{opacity:.9;transform:translateY(-2px)}@media(max-width: 768px){.packageGrid{grid-template-columns:1fr}.package.recommended{transform:none}.package.recommended:hover{transform:translateY(-5px)}}